</style>

<template>
	<div class="page-container game">
		<VonHead>
			<div slot="left" class="back-arrow white" @click="$router.push({path: '/'})"></div>
			<div slot="middle" class="title white">时间刚刚好</div>
		</VonHead>

		<div :class="['timer-wrapper', `count-${peopleCount}`]">
			<template>
				<div v-for="i in timerOptionArray" :key="i.id" :class="[peopleCount < 4 && 'diamont-timer', peopleCount > 3 && 'square-timer', `timer${i.id}`, i.isFixed && 'timeover']">
					<TimeCalculator :class="{'diamont': peopleCount < 4}" :timeCount="timeCount" :timerOption="i" @clickFixed="clickFixed"/>
				</div>
			</template>
		</div>

		<div class="layer" v-show="isLayerShow">
			<template v-if="!isGameOver">
				<div class="border-btn start-btn" @click="onClickStart"></div>
			</template>
			<template v-if="isGameOver">
				<div class="marks-wrapper" v-if="peopleCount > 1">
					<div v-for="i in timerOptionArray" :key="i.id" :class="['mark', i.isLoser && 'loser']">
						{{i.name}} {{i.timeCount}}
					</div>
				</div>
				<div class="one-player" v-else-if="peopleCount === 1 && !oneSuccess">
					<div class="end-time">{{timerOptionArray[0].timeCount}}</div>
					<p>还是{{i < standard - applyRange ? '快' : '慢'}}了一点</p>
					<p>乖乖接受惩罚吧！</p>
				</div>
				<div class="one-player" v-else-if="peopleCount === 1 && oneSuccess">
					<div class="end-time">{{timerOptionArray[0].timeCount}}</div>
					<p>真是神手速</p>
				</div>
				<div class="border-btn punish-btn" v-if="!oneSuccess" @touchend="onClickPunish" @click="onClickPunish"></div>
				<div class="border-btn one-again-btn" v-if="oneSuccess" @touchend="Reset" @click="Reset"></div>
				<div class="again-btn" v-if="!oneSuccess" @touchend="Reset" @click="Reset"></div>
			</template>
		</div>

	</div>
</template>

<script>
import VonHead from '@/components/VonHead.vue'
import {store, mutation} from '@/assets/js/store.js'
import TimeCalculator from '@/components/TimeCalculator.vue'

export default {
	name: 'game',

	data() {
		return {
			isLayerShow: true,
			isGameOver: false,
			i: 0,
			interval: null,
			timerOptionArray: [],
			standard: 500,
			applyRange: 10
		}
	},

	components: {
		VonHead,
		TimeCalculator
	},

	computed: {
		peopleCount: {
			get() {
				return store.peopleCount
			},

			set(count) {
				mutation.setPeopleCount(count)
			}
		},

		timeCount() {
			return (this.i / 100).toFixed(2)
		},

		oneSuccess() {
			if (store.peopleCount === 1) {
				return Math.abs(this.i - this.standard) <= this.applyRange
			}else {
				return false
			}
		}
	},

	methods: {
		onClickStart() {
			this.isLayerShow = false
			this.timersStart = true
			this.interval = setInterval(() => {
				this.i++
				if (this.i >= 999) {
					clearInterval(this.interval)
					this.isGameOver = true
					this.isLayerShow = true
					this.timerOptionArray.forEach(e => {
						e.isLoser = e.timeCount === null
						e.isFixed = true
						e.timeCount ? '' : e.timeCount = this.timeCount
					})
				}
			}, 10)
		},

		clickFixed(id) {
			const timerOption = this.timerOptionArray.find(e => e.id === id)
			timerOption.timeCount = this.timeCount
			timerOption.isFixed = true

			if (this.peopleCount === 1) {
				this.isGameOver = true
				this.isLayerShow = true
				clearInterval(this.interval)
				return;
			}

			this.checkOver()
		},

		checkOver() {
			if (this.timerOptionArray.every(e => e.isFixed)) {
				this.getLosers()
				this.isGameOver = true
				this.isLayerShow = true
				clearInterval(this.interval)
			}
		},

		getLosers() {
			const loser = this.timerOptionArray.map(e => e).sort((a, b) => Math.abs(b.timeCount - this.standard) - Math.abs(a.timeCount - this.standard))[0]
			loser.isLoser = true
			this.timerOptionArray.filter(e => {
				if (e.timeCount === loser.timeCount) {
					e.isLoser = true
				}
			})
		},

		onClickPunish(e) {
			e.preventDefault()

			this.$router.push({
				path: '/punishment'
			})
		},

		Reset(e) {
			e.preventDefault()
			
			clearInterval(this.interval)
			this.i = 0
			this.interval = null
			this.isGameOver = false
			this.initialData()
		},

		initialData() {
			const nameObj = {
				1: '玩家一',
				2: '玩家二',
				3: '玩家三',
				4: '玩家四',
				5: '玩家五',
				6: '玩家六',
			}
			this.timerOptionArray = Array.from(new Array(this.peopleCount)).map((e, i) => e = {
				id: ++i,
				name: nameObj[i],
				timeCount: null,
				isFixed: false
			})
		}
	},

	created() {
		this.initialData()
	}
}
</script>