import Vue from 'vue'

let ls_peopleCount = localStorage.getItem('peopleCount') && Number(localStorage.getItem('peopleCount')) || 1

export const store = Vue.observable({
	peopleCount: ls_peopleCount
})

export const mutation = {
	setPeopleCount(count) {
		store.peopleCount = count
		localStorage.setItem('peopleCount', count)
	}
}