</style>

<template>
	<div class="page-container punishment-container">
		<von-head v-show="!punishWay">
			<div slot="left" class="back-arrow white" @click="$router.push({path: '/'})"></div>
		</von-head>
		<div class="punishment-title"></div>
		<div class="ways-wrapper">
			<div class="way" v-for="way in ways" :key="way.name" @click="onClickWay(way.imgName)">
				<img :src="`https://vxiaocheng-jh.oss-cn-beijing.aliyuncs.com/H5_games/punishment/${way.imgName}.png`">
			</div>
		</div>
		<div class="try-again" @click="onClickAgain"></div>
		<div class="layer" v-show="punishWay">
			<div :class="['punishment-card', `${punishWay}`]">
				<p class="punish-text">{{punishText}}</p>
				<div class="another-btn" @click="onClickAnother">换一个</div>
			</div>
			<div class="close-btn" @click="onClickCloseSelect"></div>
		</div>

		<div class="advertisement" v-if="false">广告位</div>
	</div>
</template>

<script>
import pd from './punishData.js'
	
export default {
	data() {
		return {
			ways: [
				{
					name: '发红包',
					imgName: 'envelope'
				},
				{
					name: '喝酒',
					imgName: 'drink'
				},
				{
					name: '真心话',
					imgName: 'truth'
				},
				{
					name: '大冒险',
					imgName: 'risk'
				}
			],
			punishWay: '',
			punishText: ''
		}
	},

	computed: {

	},

	methods: {
		onClickWay(wayName) {
			this.punishWay = wayName

			this.onClickAnother()
		},

		onClickAgain() {
			this.$router.push({
				path: '/game'
			})
		},

		onClickAnother() {
			const i = Math.floor(Math.random() * pd.data[this.punishWay].length)
			if (this.punishText === pd.data[this.punishWay][i]) {
				this.onClickAnother()
				return;
			}
			this.punishText = pd.data[this.punishWay][i]
		},

		onClickCloseSelect() {
			this.punishWay = ''
		}
	},

	created() {
		this.$route.query && this.$route.query.punishWay && this.onClickWay(this.$route.query.punishWay)
	}
}
</script>