<script>
import VonHead from '@/components/VonHead.vue'
import {store, mutation} from '@/assets/js/store.js'

export default {
	name: 'home',

	components: {
		VonHead
	},

	computed: {
		peopleCount: {
			get() {
				return store.peopleCount
			},

			set(count) {
				mutation.setPeopleCount(Number(count))
			}
		}
	},

	methods: {
		onClickStart(e) {
			e.preventDefault()
			
			this.$router.push({
				path: '/game'
			})
		}
	}
