<style lang="scss" scoped>
@import '@/assets/sass/mixin.scss';

.calculator-container {
	@include flex-center();
	color: #fff;
	font-weight: bold;
	height: 100%;
	left: 51%;
	position: absolute;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	z-index: 6;

	&.diamont {
		height: 60%;
		transform: translate(-50%, -50%) rotate(45deg);
		width: 60%;

		.wrapper {
			transform: rotate(-45deg);
		}
	}

	.wrapper {
		padding-top: 10vw;

		.calculator-time {
			font-size: 15vw;
		}

		.tip {
			font-size: 5vw;
		}
	}
}
</style>

<template>
	<div class="calculator-container" @touchstart="onClickSelf" @click="onClickSelf">
		<div class="wrapper">
			<div class="calculator-time">{{timerOption.timeCount ? timerOption.timeCount : timeCount}}</div>
			<div class="tip">{{timerOption.isFixed ? '已定格' : '点击定格'}}</div>
		</div>
	</div>
</template>

<script>
	
export default {
	props: {
		timerOption: {
			type: Object,
			default: () => {
				return {
					id: 0,
					timeCount: null,
					isFixed: false
				}
			}
		},
		timeCount: {
			type: String,
			default: '0.00'
		}
	},

	data() {
		return {
		}
	},

	computed: {
	},

	watch: {
	},

	methods: {
		onClickSelf(e) {
			e.preventDefault()

			if (this.timerOption.timeCount) return;
			this.$emit('clickFixed', this.timerOption.id)
		}
	}
}
</script>